<template>
  <Extension/>
</template>

<script>

    import Extension from './components/Extension';
    import storage from './shared/storage';
    import regKeyAuth from './shared/regkey';
    import { globalStore } from './main';

        
export default {
  name: "App",

  components: {
    Extension,
  },

  data() {
    return {
      regKey: ''
    }
  },

